// Colors
// ---------------------------------

// Default

$color-default-black: #000000;
$color-default-white: #ffffff;
$color-transparent: rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-neon-blue: #2c39f2;
$color-torch-red: #ff1553;
$alice-blue: #f9fbfd;
$pattens-blue: #d9e6f4;
$tangaroa: #011c40;
$free-speech-red: #cc0000;
$apple: #6aa84f;
$dark-cerulean: #0b4880;
$quartz: #d8d8d9;
$java: #339a9a;
$sapphire: #101a56;
$sail: #a4ccf1;
$cinnabar: #d93c3c;

// Gradient

$black-to-right: linear-gradient(
  90deg,
  rgba(0, 0, 0, 0.75) 0%,
  rgba(0, 0, 0, 0) 75%
);

// Typography
// ---------------------------------

//    html   
$fz-default: 16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble: "Rouble", "Arial", sans-serif;

$ff-placeholder: "Placeholder", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default: ease;
$trans-default: 0.3s $tf-default;

//      $tf-default,     
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600: 0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320: 320px;
$vp-375: 375px;
$vp-768: 768px;
$vp-1024: 1024px;
$vp-1280: 1280px;
$vp-1440: 1440px;
$vp-1920: 1920px;

$mobile-size-max: 767px;
$tablet-size-min: 768px;
$tablet-size-max: 1023px;
$desktop-size-min: 1024px;

// Retina
// ---------------------------------

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
